@use "base/variables" as v;
@use "base/mixins" as m;

.no-proyectos {
  a {
    color: v.$indigo;
    text-align: center;
  }
}
.listado-proyectos {
  list-style-type: none;
  padding: 0;
  margin-top: 3rem;

  @include m.grid(1, 2rem);

  @include m.tablet {
    @include m.grid(2, 2rem);
  }
  @include m.desktop {
    @include m.grid(3, 2rem);
  }
}

.proyecto {
  background-color: lighten(v.$indigo, 5%);
  min-height: 15rem;
  display: flex;
  align-items: center;
  padding: 2rem;
  line-height: 2;
  border-radius: 5px;

  &:hover {
    background-color: darken(v.$indigo, 5%);
  }
  a {
    color: v.$blanco;
    margin: 0 auto;
  }
}
