@use "variables" as v;
@use "mixins" as m;

h1,
h2,
h3 {
  font-family: v.$fuente_headings;
  margin: 0 0 calc(v.$separacion / 2) 0;
  font-weight: 900;
}
h1 {
  font-size: 6rem;
}
h2 {
  font-size: 5rem;
}
h3 {
  font-size: 4rem;
}
p {
  font-size: 2rem;
  color: v.$gris;
  line-height: 1.8;
}
.tagline {
  font-family: v.$fuente_headings;
  font-weight: 700;
  color: v.$negro;
  font-size: 1.5rem;
  text-align: center;
  @include m.tablet {
    font-size: 2rem;
  }
}
.descripcion-pagina {
  text-align: center;
}
