@use "base/variables" as v;
@use "base/mixins" as m;

.modal {
  position: absolute;
  background-color: rgb(0 0 0 / 0.8);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  .formulario {
    max-width: 60rem;
    background-color: v.$blanco;
    padding: 2rem;
    margin: 0 auto;
    transform: translateY(-30rem);
    transition: transform 600ms, opacity 600ms ease-out;
    opacity: 0;
    border-radius: 1rem;

    legend {
      font-size: 2rem;
      text-align: center;
      font-weight: bold;
      margin-bottom: 2rem;
      display: block;
    }
    input[type="text"] {
      border: 1px solid v.$grisOscuro;
      border-radius: 0.5rem;
      background-color: v.$grisClaro;
    }
    .opciones {
      display: flex;
      justify-content: space-between;
    }
    &.animar {
      position: relative;
      top: 50%;
      transform: translateY(-50%);
      opacity: 1;
    }
    &.cerrar {
      transform: translateY(-200%);
      opacity: 0;
    }
  }
}
