@use "base/variables" as v;
@use "base/mixins" as m;

.barra-mobile {
  background-color: v.$indigo;
  padding: 2rem 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  @include m.tablet {
    display: none;
  }
  h1 {
    color: v.$blanco;
    margin: 0;
    font-size: 2rem;
  }
  .menu {
    height: 2rem;
    img {
      width: 2rem;

      &:hover {
        cursor: pointer;
      }
    }
  }
}

.barra {
  display: none;

  @include m.tablet {
    background-color: v.$blanco;
    padding: 1rem 2rem;
    box-shadow: 1px 12px 9px -13px rgba(0, 0, 0, 4);
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  p {
    margin: 0;
    font-size: 1.4rem;
    span {
      font-weight: 700;
      color: v.$indigo;
    }
  }
}
